import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { MenuItemInnerProps } from '@ali/4ever-component';

;
;
import { toggleSub } from '../actions';

// 目前 menu 不支持样式上的禁用，待统一调整
export default function SubMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;

  const isActive = controller.query('isSub');
  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = isCollapsedAtEdge
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInHr')
    || controller.query('isSelectionInMention')
    || controller.query('isSelectionInLink')
    || controller.query('isSelectionInListSymbol');

  const handleClick = () => controller.run('onAction', toggleSub());

  return (
    <MenuItem {...rest} onClick={handleClick} selected={isActive} disabled={isDisabled} role={PluginRoles.vertAlignSub}>
      {title}
    </MenuItem>
  );
}
